@import url(https://fonts.googleapis.com/css2?family=Marcellus&display=swap);
html {
  margin: 0;
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
main{
  height: 100%;
  margin: 0;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3),rgba(0, 0, 0, 0.8));
}

.cloud {
  background-image: url(/weather_app/static/media/clouds.8b307f63.webp);
}
.rain {
  background-image: url(/weather_app/static/media/rain.01a88891.webp);
  
}
.snow {
  background-image: url(/weather_app/static/media/snow.24ac4010.webp);
}
.sun {
  background-image: url(/weather_app/static/media/sun.ea887b1f.webp);
}

.App {
  text-align: center;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}

.img-icon {
  max-width: 80%;
  height: auto;
}
.card-font{
  font-family: 'Marcellus', serif
}


body {
  font-family: 'Marcellus';
}

.card {
  width: 9;
  padding: 1rem;
  margin: 1rem;
  border-radius: 1rem;
  background-color: rgba(170, 154, 154, 0.22);
  border-color: rgba(217, 217, 217, 0.22);
  text-align: center;
}

.date {
  align-self: flex-start;
  margin-left: -0.5rem;
  margin-top: -2rem;
  z-index: 999;
}

.icon {
  align-self: flex-end;
  margin-right: -1.4rem;
  margin-top: -2rem;
  z-index: 999;
}

.icon img {
  width: 5rem;
}

.form input {
  background-color: rgba(104, 97, 97, 0.22);
  color: white;
  border: none;
  border-radius: 10px;
  color: rgb(243, 243, 243);
  font-size: small;
}

.form input::-webkit-input-placeholder {
    color: rgb(243, 243, 243); 
  }

.form input:-ms-input-placeholder {
    color: rgb(243, 243, 243); 
  }

.form input::placeholder {
    color: rgb(243, 243, 243); 
  }

.form input:focus {
  background-color: rgb(104, 97, 97, 0.22);
  box-shadow: none !important;
  color: rgb(243, 243, 243);
}

.alert{
    background-color: rgb(104, 97, 97, 0.22);
    color: rgb(243, 243, 243);
}

.weather-name{
  font-size: 35px;
}

table td{
  padding-top: 5px;
}

@media screen and (min-device-width: 850px) {
  .weather-card{
    width:50%;
  }
  .weather-name{
    font-size: 45px;
  }
}

